import React, { useEffect } from 'react';
import { useApiService } from './useApiService';
import { ETH_BLOCK_NUMBER } from '@/constant';
import { hexToDecimal } from '../utils';

const useTransaction = () => {
  const { useMutation } = useApiService();

  const { mutateAsync: fetchBlockNumber, isPending: isBlockNumberLoading } =
    useMutation<any>({
      //   config: {
      //     select: (data: any) => parseInt(data.result, 16),
      //     staleTime: 10000, // 10 seconds
      //   },
    });

  const handleFetchBlockNumber = async () => {
    const data = await fetchBlockNumber({
      method: ETH_BLOCK_NUMBER,
      params: [],
      id: 83,
    });
    console.log('Block Number Data:', data);
    console.log('Last Block Number:', hexToDecimal(data?.result));
  };

  useEffect(() => {
    handleFetchBlockNumber();
  }, []);

  return { transactions: [], transactionsLoading: false };
};

export default useTransaction;
